import React, { useState, useContext } from 'react'
import { GlobalContext } from '../contexts/GlobalState';

export const AddTransaction = () => {
    //useState di panggil dalam function component untuk menambahkan suatu state lokal
    //useState memberikan dua hal: nilai state saat ini dan fungsi untuk memperbarui nilai tersebut
    const [text, setText] = useState('');
    const [amount, setAmount] = useState(0);

    //mengambil addTransaction dr GlobalContext
    const { addTransaction } = useContext(GlobalContext);

    //menambah transaction ketika form submit
    const onSubmit = e => {
        e.preventDefault();

        //text & amount dr state di atas => diambil dr form
        const newTransaction = {
            id: Math.floor(Math.random() * 100000000),
            text,
            amount: +amount //mengubah amount dr text jd angka
        }

        addTransaction(newTransaction);
    }

    return (
        <>
            <h3>Add new transaction</h3>
            <form onSubmit={onSubmit}>
                <div className="form-control">
                    <label htmlFor="text">Text</label>
                    {/* setText memperbarui nilai text */}
                    <input type="text" value={text} onChange={(e) => setText(e.target.value)} placeholder="Enter text..." />
                </div>
                <div className="form-control">
                    <label htmlFor="amount">Amount
                    <br />
                    (negative - expense, positive - income)
                    </label>
                    {/* setAmount memperbarui nilai amount */}
                    <input type="number" value={amount} onChange={(e) => setAmount(e.target.value)} placeholder="Enter amount..." />
                </div>
                <button className="btn">Add transaction</button>
            </form>
        </>
    )
}